                <div class="button" @click="pashalka"></div>
            </div>

            <div class="controls right">
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
                <div class="button" @click="pashalka"></div>
