</template>

<script>
import axios from 'axios';
export default {
    name: "BankComponent",
    props: {
        playerData: Object,
    },
    data() {
        return {
            page: 1,
            chanse: 5,
            withdraw: undefined,
            deposit: undefined,
            money: this.playerData.money,
            targetName: undefined,
            targetSum: undefined,
            AmountList: [],
            activeAmount: {},
            activeAmountIndex: -1,

            bsodNumb: 0,
        }
    },
    methods: {
        login() {
            var numb = this.randomize();
            this.page = 0;
            setTimeout(() => {
                this.page = 2;
            }, 200);
            setTimeout(() => {
                if (numb <= this.chanse) {
                    this.page = 99;
                } else {
                    this.page = 3;
                }
            }, 2000);
            
        },
        changePage(index) {
            this.page = 0;
            this.bsodNumb = 0;
            setTimeout(() => {
                this.page = 2;
            }, 200);
            setTimeout(() => {
                this.page = index;
            }, 1000);
        },
        pageAmount(index) {
            this.changePage(91);
            this.activeAmountIndex = index;
            this.activeAmount = this.AmountList[index];
        },
        payafine() {
            //  .   ,  
            //var success = true;
            //this.AmountList[this.activeAmountIndex].IsPaid = true;

            axios.post('https://eclipse/PayTicket', {
                Id: this.AmountList[this.activeAmountIndex].Id
            }).then(() => { return; }).catch(function () { console.log() });

        },
        randomize() {
            return Math.ceil(Math.random()*100)
        },
         exit() {
            axios.post('https://eclipse/CloseATM', {

            }).then(() => { return; }).catch(function () { console.log() });
        },
        tryCharge() {
            var sum = parseInt(this.withdraw)
            if (sum > 0) {
                this.charge(sum)
            }
        },
        tryDeposit() {
            var sum = parseInt(this.deposit)
            if (sum > 0) {
                this.cashIn()
            }
        },
        tryTransfer() {
            var sum = parseInt(this.targetSum)
            if (sum > 0) {
                this.transfer()
            }
        },
        charge(numb) {
            axios.post('https://eclipse/WithdrawMoney', {
                Amount: numb
            }).then(() => { return; }).catch(function () { console.log() });
            setTimeout(() => {
                this.withdraw = undefined;
            }, 200);
        },
        cashIn() {
            // ,   
            
            //
            // if (success) {
            //     this.changePage(30);
            // } else {
            //     this.changePage(31);
            // }
            axios.post('https://eclipse/DepositMoney', {
                Amount: this.deposit
            }).then(() => { return; }).catch(function () { console.log() });
            setTimeout(() => {
                this.deposit = undefined;
            }, 200);
        },
        transfer() {
            //        
            // this.targetName
            // this.targetSum
            //
            axios.post('https://eclipse/TransferMoney', {
                    TargetName: this.targetName,
                    TargetSum: this.targetSum
                }).then(() => { return; }).catch(function () { console.log() });

            setTimeout(() => {
                this.targetName = undefined;
                this.targetSum = undefined;
            }, 200);

        },
        getPrice(numb) {
            var elem = String(numb);
            var result = elem;
            return result.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
        },
        pashalka() {
            this.bsodNumb += 1;
            if (this.bsodNumb > 10 && this.page != 98) {
                this.bsodNumb = 0;
                this.page = 98;
            }
        },
        reload() {
            this.page = 2;
            setTimeout(() => {
                this.page = 1;
            }, 5000);
        },
        getDate(date) {
            var newdate = new Date(date);
            if (newdate) {
                var year = newdate.getFullYear();
                var month = ("0" + (newdate.getMonth() + 1)).slice(-2);
                var day = ("0" + newdate.getDate()).slice(-2);
                var time = ("0" + newdate.getHours()).slice(-2);
                var minutes = ("0" + newdate.getMinutes()).slice(-2);

                return time + ":" + minutes + ' ' + day + "." + month + "." + year;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        getType(type) {
            switch(type) {
                case 0:
                    return "";
                case 1: 
                    return " ";
                case 2:
                    return " ";
                case 3:
                    return "  ";

            }
        }
    },
    mounted() {
        // Test data
        // this.AmountList = [
        //     {"Id": 0, "Type": 0, "Author" : "Billy Donovan", "Date":"Thu Aug 19 2021 15:23:52 GMT+0300 (,  )", "Label": "Headache pills X 1", "Amount": 300, "IsPaid": false},
        //     {"Id": 1, "Type": 0, "Author" : "Genry Morgan", "Date":"Thu Aug 16 2021 11:23:52 GMT+0300 (,  )", "Label": "For Papers", "Amount": 1500, "IsPaid": true},
        //     {"Id": 2, "Type": 0, "Author" : "Eva Elfie", "Date":"Thu Aug 12 2021 15:23:52 GMT+0300 (,  )", "Label": "Insurance for week", "Amount": 900, "IsPaid": false},
        //     {"Id": 3, "Type": 1, "Author" : "Officer Jessy Pinkman", "Date":"Thu Aug 10 2021 15:23:52 GMT+0300 (,  )", "Label": "Exceeding the speed limit by 20 km per hour", "Amount": 500, "IsPaid": false},
        //     {"Id": 0, "Type": 2, "Author" : "Genry Morgan", "Date":"Thu Aug 19 2021 15:23:52 GMT+0300 (,  )", "Label": "Headache pills X 1", "Amount": 300, "IsPaid": false},
        //     {"Id": 1, "Type": 2, "Author" : "Genry Morgan", "Date":"Thu Aug 16 2021 11:23:52 GMT+0300 (,  )", "Label": "For Papers", "Amount": 1500, "IsPaid": true},
        //     {"Id": 2, "Type": 2, "Author" : "Johny Cage", "Date":"Thu Aug 12 2021 15:23:52 GMT+0300 (,  )", "Label": "Insurance for week", "Amount": 900, "IsPaid": false},
        //     {"Id": 3, "Type": 2, "Author" : "Officer Jessy Pinkman", "Date":"Thu Aug 10 2021 15:23:52 GMT+0300 (,  )", "Label": "Exceeding the speed limit by 20 km per hour", "Amount": 500, "IsPaid": false},
        
        // ]
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadTickets") {
                this.AmountList = event.data.data;
            }
            else if(event.data.type == "LoadBankData") {
                console.log(event.data.invoices)
                this.money = event.data.Money;
                this.AmountList = JSON.parse(event.data.invoices);
            }
            else if(event.data.type == "DepositError") {
                this.changePage(31);
            }
            else if(event.data.type == "WithdrawError") {
                this.changePage(21);
            }
            else if(event.data.type == "OnlineError") {
                //
            }
            else if(event.data.type == "TransferError") {
                this.changePage(41);
            }
            else if(event.data.type == "DepositSuccess") {
                this.changePage(30);
            }
            else if(event.data.type == "WithdrawSuccess") {
                this.changePage(20);
            }
            else if(event.data.type == "TransferSuccess") {
                this.changePage(40);
            }
            else if(event.data.type == "TicketSuccess") {
                this.changePage(92);
            }
            else if(event.data.type == "TicketError") {
                this.changePage(93);
            }
            else if(event.data.type == "LoadLanguage") {
                this.translate = JSON.parse(event.data.translate);
            }
        },

        false
        );
    }
}
</script>

<style scoped>
.loader {
    margin: 100px auto;
    font-size: 25px;
    width: 1em;
    height: 1em;
    border-radius: 50%;
    position: relative;
    top: 32%;
    text-indent: -9999em;
    animation: load5 1.1s infinite ease;
    transform: translateZ(0);
}
@-webkit-keyframes load5 {
  0%,
  100% {
    box-shadow: 0em -2.6em 0em 0em #ffffff, 1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2), 2.5em 0em 0 0em rgba(255, 255, 255, 0.2), 1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2), 0em 2.5em 0 0em rgba(255, 255, 255, 0.2), -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2), -2.6em 0em 0 0em rgba(255, 255, 255, 0.5), -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.7);
  }
  12.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.7), 1.8em -1.8em 0 0em #ffffff, 2.5em 0em 0 0em rgba(255, 255, 255, 0.2), 1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2), 0em 2.5em 0 0em rgba(255, 255, 255, 0.2), -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2), -2.6em 0em 0 0em rgba(255, 255, 255, 0.2), -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.5);
  }
  25% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.5), 1.8em -1.8em 0 0em rgba(255, 255, 255, 0.7), 2.5em 0em 0 0em #ffffff, 1.75em 1.75em 0 0em rgba(255, 255, 255, 0.2), 0em 2.5em 0 0em rgba(255, 255, 255, 0.2), -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2), -2.6em 0em 0 0em rgba(255, 255, 255, 0.2), -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2);
  }
  37.5% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.2), 1.8em -1.8em 0 0em rgba(255, 255, 255, 0.5), 2.5em 0em 0 0em rgba(255, 255, 255, 0.7), 1.75em 1.75em 0 0em #ffffff, 0em 2.5em 0 0em rgba(255, 255, 255, 0.2), -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2), -2.6em 0em 0 0em rgba(255, 255, 255, 0.2), -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2);
  }
  50% {
    box-shadow: 0em -2.6em 0em 0em rgba(255, 255, 255, 0.2), 1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2), 2.5em 0em 0 0em rgba(255, 255, 255, 0.5), 1.75em 1.75em 0 0em rgba(255, 255, 255, 0.7), 0em 2.5em 0 0em #ffffff, -1.8em 1.8em 0 0em rgba(255, 255, 255, 0.2), -2.6em 0em 0 0em rgba(255, 255, 255, 0.2), -1.8em -1.8em 0 0em rgba(255, 255, 255, 0.2);
