<template>
    <div class="content_outer busdriver" >
        <div class="main_content" v-if="page == 0">
            <h2 class="text-center">  </h2>
            <p class="justify">    .    ,         <b>  </b>  ,   ?</p>
            <p class="justify">   ,    .    -,      ,        .</p>
            <p class="justify">            ,     .  !     ,  .</p>
            <div class="menu">
                <button v-if="!$parent.jobActive" class="btn btn-primary" @click="changePage(1)">  </button>
                <button v-else class="btn btn-primary" @click="stopJob()">  </button>
                <button class="btn btn-secondary" @click="exit()"></button>
            </div>
        </div>

        <div class="main_content" v-if="page == 1">
            <h2 class="text-center"> </h2>

            <div class="routes">
                <div class="route_item" v-for="item in routes" :key="'route_'+item.Id">
                    <div class="row">
                        <div class="col-8">
                            <h5>{{item.Name}}</h5>
                            <p>: {{item.RouteName}}<br>{{item.Description}}</p>
                        </div>
                        <div class="col-4 d-flex align-items-center">
                            <button class="btn btn-primary btn-block" @click="startJob(item.Id)"></button>
                        </div>
                    </div>
                </div>
            </div>

            <div class="menu">
                <button class="btn btn-secondary" @click="changePage(0)"></button>
            </div>
        </div>

        <div class="person"></div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    name: 'JobBusdriver',
    props: {
        playerData: Object
    },
    components: {

    },
    data() {
        return {
            page: 0,
            routes: [
                {
                    Name: " 2",
                    RouteName: " - ",
                    Description: "  ",
                    Id: 0,
                },
                {
                    Name: " 4",
                    RouteName: " - ",
                    Description: "     ",
                    Id: 1,
                },
                {
                    Name: " 10",
                    RouteName: "  -  ",
                    Description: "     .",
                    Id: 2,
                },
                {
                    Name: " 14",
                    RouteName: " - ",
                    Description: "  ??",
                    Id: 3,
                },
                {
                    Name: " 16",
                    RouteName: " - ",
                    Description: "  .",
                    Id: 4,
                },
                {
                    Name: " 20",
                    RouteName: "  -  -",
                    Description: " - ",
                    Id: 5,
                },
                {
                    Name: " 21",
                    RouteName: " -  ",
                    Description: " - ",
                    Id: 6,
                },
                {
                    Name: " 30",
                    RouteName: "  -  -",
                    Description: "    ",
                    Id: 7,
                },
                {
                    Name: " 33",
                    RouteName: "  -  -",
                    Description: "  ",
                    Id: 8,
                },
                {
                    Name: " 45",
                    RouteName: "  -  -",
                    Description: "  ",
                    Id: 9,
                },
                {
                    Name: " 60",
                    RouteName: " -  ",
                    Description: "    ",
                    Id: 10,
                }
            ]
        }
    },
    methods: {
        changePage(numb) {
            if (this.page !=numb) {
                this.page = numb;
            }
        },
        startJob(numb) {
            axios.post('https://eclipse/BusDriverStartWorkDay', {
                RouteId: numb
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/BusDriverStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
        },
        exit() {
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
            axios.post('https://eclipse/ExitJobOffer', {
            }).then(() => { return; }).catch(function () { console.log() });
        }
    }
}

</script>