<template>
    <div class="content_outer lawnmover">
        <div class="main_content">
            <h2 class="text-center"> </h2>
            <p class="justify" v-if="playerData.sex"> <b>  </b>,       ?              ?</p>
            <p class="justify" v-else> <b>  </b>,       ?              ?</p>
            <p class="justify">  -    !  ,        <b> </b>.  ,         -,      .  ,    .</p>
            <p class="justify"> ,  ,  ,   :        .      <b></b>.   !</p>
            <div class="menu">
                <button v-if="!$parent.jobActive" class="btn btn-primary" @click="startJob()"> </button>
                 <button v-else class="btn btn-primary" @click="stopJob()">  </button>
                <button class="btn btn-secondary" @click="exit()"></button>
            </div>
        </div>
        <div class="person"></div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'JobLawnmover',
    props: {
        playerData: Object
    },
    components: {

    },
    data() {
        return {
            
        }
    },
    methods: {
        startJob() {
            axios.post('https://eclipse/LawnmowerStartWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/LawnmowerStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
        },
        exit() {
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
            axios.post('https://eclipse/ExitJobOffer', {
            }).then(() => { return; }).catch(function () { console.log() });
        }
    }
}

</script>