<template>
    <div class="page page_1 app_business">
        <div class="content">

            <transition name="transition_02">
                <div class="page_2" v-if="page == 0">
                    <div class="header">
                        <div class="d-flex align-items-center">
                            <div class="app_image">
                                <div class="circle"></div>
                            </div>
                            <div class="headings">
                                <h2> </h2>
                                <h3>    ...</h3>
                            </div>
                        </div>
                        <button class="btn btn-business" @click="exitApp()"></button>
                        
                    </div>
                    <h4>   :</h4>
                    <div class="business_list scrolled">
                        <div class="business_item bus_style" v-for="(item, index) in businessList" :key="'business'+ index" @click="openBusiness(index)">
                            <h3>{{item.Name}}</h3>
                            <span class="address">{{item.Adress}}</span>
                            <div class="text-right">
                                <span v-if="item.Missions.length || item.Missions.length > 0"><b> :</b> {{item.Missions.length}}</span>
                                <span v-else><b>  </b></span>
                            </div>
                        </div>
                    </div>

                </div>
            </transition>

            <transition name="transition_02">
                <div class="page_1 scrolled" v-if="page == 2">
                    <div class="d-flex justify-content-between align-items-center">
                        <div class="headings">
                            <h2>{{activeBusiness.Name}}</h2>
                            <h3 class="mt-2">{{activeBusiness.Adress}}</h3>
                        </div>
                        <button class="btn btn-business" @click="changePage(0)"></button>
                    </div>
                    <div class="des_block">
                        <div class="d-flex">
                            <div class="m_block">
                                <span class="heading"></span>
                                <span class="">$ <span class="money">{{activeBusiness.Bank}}</span></span>
                            </div>
                            <div class="m_block">
                                <span class="heading"></span>
                                <span class="">$ <span class="money2">{{activeBusiness.Spent}}</span></span>
                            </div>
                            <div class="m_block" v-if="activeBusiness.Employees">
                                <span class="heading"></span>
                                <span class=""><span class="money"><b-icon icon="person-fill" class="m_icon"></b-icon> {{activeBusiness.Employees.length}}</span></span>
                            </div>
                        </div>
                        <div class="d-flex align-items-center">
                            <div v-if="activeBusiness.BusinessType!=11" class="btn btn-outline-business" :class="{active: subpage == 1}" @click="changeSubpage(1)"></div>
                            <div v-if="activeBusiness.BusinessType!=11" class="btn btn-outline-business" :class="{active: subpage == 4}" @click="changeSubpage(4)"></div>
                            <div v-if="activeBusiness.BusinessType==11" class="btn btn-outline-business" :class="{active: subpage == 5}" @click="changeSubpage(5)"></div>
                            <div class="btn btn-outline-business" :class="{active: subpage == 2}" @click="changeSubpage(2)"></div>
                            <div class="btn btn-outline-business" :class="{active: subpage == 3}" @click="changeSubpage(3)"></div>
                        </div>
                    </div>
                    <transition name="list" v-if="subpage == 1">
                        <div class="business_block">
                            <div class="left_block">
                                <div class="title">
                                    <h4> </h4>
                                    <hr class="hr">
                                </div>
                                <div class="task_list">
                                    <div class="task_element bus_style" v-for="(item, index) in activeBusiness.Missions" :key="'mis_' + index + item.id" @click="openTask(index)" :class="{active: item.Id == activeTask.Id}">
                                        <h3>#{{item.Id}} {{item.Name}}</h3>
                                        
                                        <div class="d-flex justify-content-between">
                                            <span class="type"> : {{item.Translate}}</span>
                                            <div> : <span>{{getTime(item.Exptime)}}</span></div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div class="right_block">
                                <div class="task_active" v-if="activeTask.Id">
                                    <h3>#{{activeTask.Id}} {{activeTask.Name}}</h3>
                                    <div v-if="activeTask.PerformingId != 0" class="owner">  ID {{activeTask.PerformingId}}</div>
                                    <div> : {{activeTask.Translate}}</div>
                                    <div>: {{activeTask.Description}}</div>
                                    <div> : {{activeTask.Exptime}}</div>
                                    <div> : {{activeTask.Route}} .</div>
                                    <div class="controls">
                                        <button class="btn btn-business" v-if="activeTask.PerformingId == 0" @click="takeTaskByPlayer()"> </button>
                                        <button class="btn btn-business" v-if="activeTask.PerformingId == playerData.id" @click="cancelTaskByPlayer()"> </button>
                                        <button class="btn btn-business" v-if="activeTask.PerformingId == 0" @click="offerTaskToNearestPlayer()">  </button>
                                        <button class="btn btn-business" v-if="activeTask.PerformingId == 0" @click="showModalC()">   id</button>
                                        <button class="btn btn-business" v-if="activeTask.PerformingId !=0 && activeTask.PerformingId != playerData.id"  @click="cancelTaskToPlayer()">   id {{activeTask.PerformingId}}</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </transition>
                    <transition name="list" v-if="subpage == 2">
                        <div class="business_block">
                            <div class="left_block">
                                <div class="title">
                                    <h4> </h4>
                                    <hr class="hr">
                                </div>
