                                    <div class="employee_item bus_style" v-for="(item, index) in activeBusiness.Employees" :key="'employee_' + index" @click="chooseEmployee(index)" :class="{active: activeEmployee.UserId == item.UserId }">
                                        <div class="index">
                                            <b>{{index}}</b>
                                        </div>
                                        <div class="data">
                                            {{item.Name}} {{item.Phone}} 
                                        </div>
                                        <div class="level">
                                            {{item.Level}}
                                        </div>
                                    </div>
                                    <div class="employee_item bus_style2" @click="addNewEmployee()">  </div>
                                </div>
                            </div>
                            <div class="right_block">
                                <div class="task_active" v-if="activeEmployee.UserId">
                                    <h3>#{{activeEmployee.UserId}} {{activeEmployee.Name}}</h3>
                                    <div>: {{activeEmployee.Phone}}</div>
                                    <div>: {{activeEmployee.Level}}</div>
                                    <div>: {{activeEmployee.Salary}}$</div>
                                    <div class="controls">
                                        <button class="btn btn-business" @click="increasePlayerLevel()"> </button>
                                        <button class="btn btn-business" @click="decreasePlayerLevel()"> </button>
                                        <button class="btn btn-business" @click="changeSalary()"> </button>
                                        <button class="btn btn-business" @click="fireEmployee()"> </button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </transition>
