                                            <h4 class="price">{{item.Price}}$/</h4>
                                        </div>
                                        <div class="col-5">
                                            <button class="btn btn-success btn-block btn-sm" v-if="item.IsFree" @click="changeRoomStatis(item.ItemId)"></button>
                                            <button class="btn btn-danger btn-block btn-sm" v-if="!item.IsFree" @click="changeRoomStatis(item.ItemId)"></button>
                                            <button class="btn btn-business btn-block btn-sm" @click="giveKeyToPlayer(item.ItemId)"> </button>
                                        </div>
                                    </div>
                                </div>
                            </div>
                                
