                    <div class="modal_content">
                        <div class="d-flex">
                            <button class="btn btn-business btn-block mr-1" @click="fireEmployeeNow()"></button>
                            <button class="btn btn-business btn-block ml-1 mt-0" @click="cancelFireEmployee()"></button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="list">
            <div class="business-modal" v-if="showModalWindowC">
                <div class="modal_inner">
                    <div class="modal_header">
                        <h4 class="heading">  </h4>
                        <button class="btn btn-business btn-sm" @click="hideModalC()"></button>
                    </div>
                    <div class="modal_content">
                        <input class="form-control mb-3" v-model="idShareTask">
                        <div class="d-flex">
                            <button class="btn btn-business btn-block" @click="offerTaskToPlayerById()"></button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

        <transition name="list">
            <div class="business-modal" v-if="showModalWindowD">
                <div class="modal_inner">
                    <div class="modal_header">
                        <h4 class="heading">  </h4>
                        <button class="btn btn-business btn-sm" @click="hideModalD()"></button>
                    </div>
                    <div class="modal_content">
                        <input class="form-control mb-3" v-model="idInviteEmployee">
                        <div class="d-flex">
                            <button class="btn btn-business btn-block" @click="inviteNewEmployee()"></button>
                        </div>
                    </div>
                </div>
            </div>
        </transition>

    </div>
</template>


