            newOrder: {
                Name: "",
                Description: "",
            }
        }
    },
    methods: {
        changePage(index) {
            if (index != this.page) {
                this.page = index;
                this.closeAllModals();
                this.subpage=0;
            }
        },
        openBusiness(index) {
            this.activeBusiness = this.businessList[index];
            this.activeTask = {};
            this.changePage(2);
        },
        closeAllModals() {
            this.showModalWindowA = false;
            this.showModalWindowB = false;
            this.showModalWindowC = false;
            this.showModalWindowD = false;
        },
        exitApp() {
            this.closeAllModals();
            this.$parent.closeAllApps();
        },
        openTask(index) {
            this.activeTask = this.activeBusiness.Missions[index];
        },
        findTranslate(type) {
            var elem = this.taskTypes.find(item => item.Type == type)
            if (elem) {
                return elem.Translate
            } else {
                return " "
            }
            
        },
        findDescription(type) {
            var elem = this.taskTypes.find(item => item.Type == type)
            if (elem) {
                return elem.Description
            } else {
                return " "
            }
        },
        showModalC() {
            this.showModalWindowC = true;
            this.idShareTask = "";
        },
        hideModalC() {
            this.showModalWindowC = false;
        },
        changeSubpage(index) {
            if (index != this.subpage) {
                this.subpage = -1;
                setTimeout(() => {
                    this.subpage = index;
                    this.activeTask = {};
                    this.activeEmployee = {};
                    this.statpage = 0;
                }, 500);
            }
        },
        chooseEmployee(index) {
            this.activeEmployee = this.activeBusiness.Employees[index];
        },
        increasePlayerLevel() {
            this.activeEmployee.Level +=1;
            axios.post('https://eclipse/increasePlayerLevel', {
                    Level: this.activeEmployee.Level,
                    Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        decreasePlayerLevel() {
            this.activeEmployee.Level -=1;
            if (this.activeEmployee.Level < 0) {
                this.activeEmployee.Level = 0;
            }
            axios.post('https://eclipse/decreasePlayerLevel', {
                    Level: this.activeEmployee.Level,
                    Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        fireEmployee() {
            this.showModalWindowB = true;
        },
        cancelFireEmployee() {
            this.showModalWindowB = false;
        },
        changeSalary() {
            this.showModalWindowA = true;
            this.newEmployeeSalary = JSON.parse(JSON.stringify(this.activeEmployee.Salary));
            axios.post('https://eclipse/changeBusinessSalary', {
                    Salary: this.activeEmployee.Salary,
                    Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        saveNewSalary() {
            this.showModalWindowA = false;
            this.activeEmployee.Salary = JSON.parse(JSON.stringify(this.newEmployeeSalary));
            axios.post('https://eclipse/changeBusinessSalary', {
                    Salary: this.activeEmployee.Salary,
                    Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        cancelNewSalary() {
            this.showModalWindowA = false;
        },
        changeStatPage(index) {
            if (index != this.statpage) {
                this.statpage = index;
            }
        },
        addNewEmployee() {
            this.showModalWindowD = true;
            this.idInviteEmployee = "";
        },
        hideModalD() {
            this.showModalWindowD = false;
        },
        takeTaskByPlayer() {
            this.activeTask.PerformingId = this.$parent.$parent.playerData.id;
            axios.post('https://eclipse/takeTaskByPlayer', {
