                    MissionName: this.activeTask.Name
            }).then(() => { return; }).catch(function () { console.log() });
        },
        offerTaskToPlayerById() {
            this.showModalWindowC = false;
            axios.post('https://eclipse/offerTaskToPlayerById', {
                    Task: this.activeTask.Id,
