                    MissionName: this.activeTask.Name
            }).then(() => { return; }).catch(function () { console.log() });
            //     id this.idShareTask
        },
        cancelTaskToPlayer() {
            //   ( ownerId)
            axios.post('https://eclipse/cancelTaskPlayer', {
                    Task: this.activeTask.Id,
                    
            }).then(() => { return; }).catch(function () { console.log() });
            this.activeTask.PerformingId = 0;
        },
        fireEmployeeNow() {
            //   this.activeEmployee
            this.showModalWindowB = false;
             axios.post('https://eclipse/fireEmployee', {
                     Id: this.activeEmployee.UserId,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        inviteNewEmployee() {
            //   this.idInviteEmployee
            axios.post('https://eclipse/inviteNewEmployee', {
                     Id: this.idInviteEmployee,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
            this.showModalWindowD = false;
        },
        changeEmployersSalary() {
            //   
            axios.post('https://eclipse/changeBusinessSalarys', {
                    Employees: this.activeBusiness.Employees,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
        },
        changePrices() {
            axios.post('https://eclipse/changeBusinessPrices', {
                    ProductsData: this.activeBusiness.ProductsData,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
            //    
        },
        getTime(timer) {
            var today = new Date(timer);
            var time = ("0" + today.getHours()).slice(-2);
            var minutes = ("0" + today.getMinutes()).slice(-2);
            var year = today.getFullYear();
            var month = ("0" + (today.getMonth() + 1)).slice(-2);
            var day = ("0" + today.getDate()).slice(-2);
            var date =
                time + ":" + minutes + " " + day + "." + month + "." + year;
            return date;
        },
        changeOrderPage(index) {
            if (index != this.orderpage) {
                this.orderpage = index;
            }
        },
        registerNewOrder() {
            axios.post('https://eclipse/registerNewOrder', {
                    Name: this.newOrder.Name,
                    Description: this.newOrder.Description,
                    BusinessId: this.activeBusiness.Id
            }).then(() => { return; }).catch(function () { console.log() });
            this.newOrder = {
                Name: "",
                Description: "",
            }
            this.orderpage = 0;
        },
        changeRoomStatis(ItemId) {
            var item = this.activeBusiness.ProductsData.find(item => item.ItemId == ItemId)
            item.IsFree = !item.IsFree;

        },
        giveKeyToPlayer(ItemId) {
            ItemId // Id 
            this.activeBusiness.Id // Id 
        }

    },

    mounted() {
        window.addEventListener("message",(event) => {
            if (event.data.type == "LoadBusinessList") {
                this.businessList = JSON.parse(event.data.businessList);
                this.businessList.forEach((element) => (element.ProductsData = JSON.parse(element.ProductsData)));
            }
            else if(event.data.type == "LoadBusinessMissions") {
                this.businessList.forEach((element) => {
                    if(element.Id == event.data.businessId) {
                        element.Missions = JSON.parse(event.data.mission);
                    }
                });
            }
        });
    }
}
</script>