<template>
    <section class="fullpage bg_test3 fullpage_weapon">
        <div class="shop_weapon">
            <div class="header">
                <div class="logo" @click="changePage(10)"></div>
                <div class="right_block">
                    <div class="shop_name">
                        <h2>Ammu <span></span> nation</h2>
                    </div>
                    <div class="main_menu">
                        <button class="btn" v-for="(item, index) in weaponFullList" :key="'menu'+index" :class="{active: page == index}" @click="changePage(index)">{{showName(item.category).name}}{{showName(item.category).name2}}<span></span></button>
                    </div>
                </div>
                <div class="cart_block" @click="changePage(100)">
                    <b-icon class="cart_icon" icon="cart2" scale="1.8" aria-hidden="true"></b-icon>
                    <span class="cart_label"> :</span>
                    <div class="total">
                        <span>: <span class="cart_count">{{count}}</span></span> - 
                        <span class="cart_items">${{fullPrice}}</span>
                    </div>
                </div>
            </div>

            <div class="body">

                <transition name="list">
                    <div class="content" v-show="page == 10">
                        <div class="top_block">
                            <h2 class="section_title"> <span>  </span></h2>
                            <div class="section_description"> -   !<br>       .</div>
                        </div>
                        <swiper class="swiper swiper_weapon" :options="swiperOptions">
                            <swiper-slide class="product" v-for="(item, index) in popularWeapons" :key="'weapons'+index">
                                <div class="image" :style="'background-image: url(' + siteName + item.image +');'"></div>
                                <div class="text_block">
                                    <h4 class="name">{{item.name}}</h4>
                                    <p class="description">{{item.description}}</p>
                                    
                                </div>
                                <h5 class="price">${{item.Price}}.00</h5>
                                <div class="hover_block">
                                    <div class="stats">
                                        <div class="stat_block">
                                            <h5 class="label_text"></h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).damage + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text"></h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).speed + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text"></h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).accuracy + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text"></h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).range + '%'"></div>
                                            </div>
                                        </div>

                                    </div>
                                    <div class="controls">
                                        <button class="btn btn-block btn-fastbuy" @click="fastBuy(item)"> </button>
                                        <button class="btn btn-block btn-buy" @click="addToCart(item.id)"> </button>
                                    </div>
                                </div>
                            </swiper-slide>
                        </swiper>
                    </div>

                </transition>

                <transition-group tag="div" name="list">
                    <div class="content" v-show="page == count" v-for="(elem, count) in weaponFullList" :key="'weapon'+count">
                        <div class="top_block">
                            
                            <h2 class="section_title">{{showName(elem.category).name}}<span>{{showName(elem.category).name2}}</span></h2>
                            <div class="section_description" v-html="showName(elem.category).description"></div>
                        </div>
                        <swiper class="swiper swiper_weapon" :options="swiperOptions" v-if="weaponFullList">
                            <swiper-slide class="product" v-for="(item, index) in elem.items" :key="'product'+index">
                                <div class="image" :style="'background-image: url(' + siteName + item.Image +');'"></div>
                                <div class="text_block">
                                    <h4 class="name">{{item.Name}}</h4>
                                    <p class="description">{{item.Description}}</p>
                                    <h5 class="license">: <span v-for="elem in item.License" :key="'lic_'+elem">{{elem}} </span></h5>
                                </div>
                                <h5 class="price">${{item.Price}}.00</h5>
                                <div class="hover_block">
                                    <div class="stats">
                                        <div class="stat_block">
                                            <h5 class="label_text"></h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).damage + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text"></h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).speed + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text"></h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).accuracy + '%'"></div>
                                            </div>
                                        </div>
                                        <div class="stat_block">
                                            <h5 class="label_text"></h5>
                                            <div class="progress">
                                                <div class="progress-bar" :style="'width:' + getItemById(item.ItemId).range + '%'"></div>
                                            </div>
                                        </div>

                                    </div>
                                    <div class="controls">
                                        <button class="btn btn-block btn-fastbuy" @click="fastBuy(item)"> </button>
                                        <button class="btn btn-block btn-buy" @click="addToCart(item)"> </button>
                                    </div>
                                </div>
                            </swiper-slide>
                        </swiper>
                    </div>

                </transition-group>

