                    <div class="content cart_page" v-show="page == 100">
                        <div class="left_bar">
                            <div class="part">
                                <h2 class="name"></h2>
                                <ul class="cart_list">
                                    <li class="cart_item" v-for="(item, index) in cartList" :key="'cartList' + index">
                                        <span class="remove" @click="remove(index)"></span>
                                        <span class="name">{{item.Name}}</span>
                                        <span class="quantity">{{item.Count}} x <span class="amount">${{item.Price}}.00</span></span>
                                        <div class="count">
                                            <button class="btn-minus" @click="minusCount(index)"></button>
                                            {{item.Count}}
                                            <button class="btn-plus" @click="plusCount(index)"></button>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                            <div class="part">
                                <div class="total_price">
