                                    <h3 class="total">${{fullPrice}}</h3>
                                </div>
                                <button class="btn btn-block btn-buy" @click="buy()"></button>
                            </div>
                        </div>
                    </div>

                </transition>

                
            </div>

        </div>


    </section>
</template>

