<template>
    <div class="content_outer postman">
        <div class="main_content">
            <h2 class="text-center"> </h2>
            <p class="justify">      <b>-</b>.   ,     :     ?</p>
            <p class="justify"> !  <b>  </b>,        ,      10 .</p>
            <p class="justify">  :     <b></b>,   <b></b>.   :   ,   !</p>
            <p v-if="playerData.sex">  <b>?</b></p>
            <p v-else>  <b></b>?</p>
            <p>         . :   ,      : !      . <b>!</b></p>
            <div class="menu">
                <button v-if="!$parent.jobActive" class="btn btn-primary" @click="startJob()"> </button>
                <button v-else class="btn btn-primary" @click="stopJob()">  </button>
                <button class="btn btn-secondary" @click="exit()"></button>
            </div>
        </div>
        <div class="person"></div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'JobPostman',
    props: {
        playerData: Object
    },
    components: {
        
    },
    data() {
        return {
            
        }
    },
    methods: {
        startJob() {
            axios.post('https://eclipse/GoPostalStartWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/GoPostalStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
        },
        exit() {
            this.$parent.jobActive = false;
            this.$parent.showJob = ""
            axios.post('https://eclipse/ExitJobOffer', {
            }).then(() => { return; }).catch(function () { console.log() });
        }
    }
}

</script>