</template>

<script>
// import playerList from "../../json/players.json";
// playerList.forEach(element => {element.show = true;});

import axios from 'axios';
import Business from './components/Business.vue';
import click from '../../assets/sounds/click.mp3';
import weaponNamesList from '../../json/weapon.json';

export default {
    name: "AdminPanel",
    props: {
        playerData: Object,
    },
    components: {
        Business,
    },
    data() {
        return {
            page: 1,
            activeFilter: 1,
            activeSort: 1,
            position: {
                posX: 0,
                posY: 0,
                posZ: 0,
                rot: 0
            },

            weaponNamesList: weaponNamesList,

            loader: false,
            iconLoading: false,

            playerList: [],
            compact: true,

            adminRanks: [
                {name: ""},
                {name: " 1"},
                {name: " 2"},
                {name: " 3"},
                {name: ""},
                {name: " "},
                {name: " "},
                {name: ""},
                {name: " "},
                {name: " "},
                {name: ""},
                {name: " "}
            ],

            activePlayer: null,

            weather: { text: '', value: "EXTRASUNNY" },
            showWeatherDropdown: false,
            weatherList: [
                { text: '', value: "EXTRASUNNY" },
                { text: ' ', value: "CLEAR" },
                { text: '', value: "NEUTRAL" },
                { text: '', value: "SMOG" },
                { text: '', value: "FOGGY" },
                { text: '', value: "OVERCAST" },
                { text: '', value: "CLOUDS" },
                { text: ' ', value: "CLEARING" },
                { text: '', value: "RAIN" },
                { text: '', value: "THUNDER" },
                { text: '', value: "SNOW" },
                { text: '', value: "BLIZZARD" },
                { text: '', value: "SNOWLIGHT" },
                { text: '', value: "XMAS" },
                { text: '', value: "HALLOWEEN" },
            ],

            statusList: [
                "",
                "",
                " ",
                " "
            ],

            timeHours: 23,
            timeMinutes: 59,
            showGiveItemWindowA: false,
            showGiveItemWindowB: false,
            showGiveItemWindow: false,
            showGiveItemWindowD: false,
            showGiveItemWindowE: false,
            giveItemModel: {
                Name: "",
                Count: 0,
            },
            giveHash: null,
            kickReason: "",
            banReason: "",
            banTime: 0,

            showEditPlayerInfoWindow: false,
            showEditPlayerAutoWindow: false,
            showEditPlayerRolesWindow: false,
            showEditPlayerDimensionWindow: false,
            showAddNewAutoWindow: false,
            editPlayerInfoModel: {},
            editPlayerAutoModel: {},
            newPlayerAutoModel: {},
            activeAutoIndex: undefined,
            newPlayerRole: "",
            searchFieldModel: "",
            newPlayerDimension: "",

            adminCalls: [],
            activeAdminCall: undefined,
            showAdminDropdown: false,

            gameChatData: [{Author: "Jessie Pinkman", AuthorId: 21,"Group": "standart", "Type": "standart", "Chat": "All", "Message": "  ..."}],
            policeCallData: [{id: 21, date: "23.12.44", status: "", message: " .    -.", crew: [155, 233], from: "123", show: true,}],
            activePoliceCall: undefined,
            activePoliceCallModel: undefined,
            medicalCallData: [],
            taxiCallData: [],
            mechanicCallData: [],
            newsCallData: [],
            bleeterData: [],
            dynastyData: [],
            darkChatData: [],
            adminCallData: [],

            createVehicleModel: {},
            createPropModel: {},
            deleteCarIndex: undefined,
            deletePropIndex: undefined,
            deletePropIndexById: undefined,

            allFoodPriceData: [{Name: "Test 1",Price: 800,Id: "foodTest1"},],
            allVehiclesPriceData: [{Name: "Weapon 1",Price: 800,Id: "weaponTest1"},],
            allEstatePriceData: [{Name: "Mirror Park 3",Price: 80000,Id: "apartTest1"},],

            showAddNewWeaponWindow: false,
            showEditWeaponWindow: false,
            newPlayerWeaponModel: {},
            showWeaponError: false,

            trafficA: 0.3,
            trafficB: 0.3,
            trafficC: 0.3,

            pageNumber: 0,
            foodDataSize: 10,
            renderFoodData: [],
        }
    },
    computed: {
        pageCount(){
            let l = this.allFoodPriceData.length,
                s = this.foodDataSize;
            return Math.ceil(l/s);
        },
        paginatedData(){
            const start = this.pageNumber * this.foodDataSize,
            end = start + this.foodDataSize;
            return this.allFoodPriceData.slice(start, end);
        }
    },
    methods: {
        changePage(index) {
            if (this.page != index) {
                this.page = index;
                this.closeAllModals();
            }

            if(index == 91 || index == 92 || index == 93) {
                axios.post('https://eclipse/GetPricesData', {
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        nextPage() {
            this.pageNumber++;
        },
        prevPage(){
            this.pageNumber--;
        },
        closeAllModals() {
            this.showGiveItemWindowA = false;
            this.showGiveItemWindowB = false;
            this.showGiveItemWindowC = false;
            this.showGiveItemWindowD = false;
            this.showGiveItemWindowE = false;
            this.showEditPlayerInfoWindow = false;
            this.showEditPlayerAutoWindow = false;
            this.showEditPlayerRolesWindow = false;
            this.showEditPlayerDimensionWindow = false;
            this.showAddNewAutoWindow = false;
            this.showAdminDropdown = false;
        },
        showPlayerPage(index) {
            this.playSound();
            this.activePlayer = this.playerList[index];
            this.page = 31;
        },
        TurnBlackout(state) {
            axios.post('https://eclipse/Blackout', {
                State: state    
            }).then(() => { return; }).catch(function () { console.log() });
        },
        changeFilter(index) {
            this.activeFilter = index;
            this.hideAllArray(this.playerList)
            switch (index) {
                case 1:
                    this.showAllArray(this.playerList)
                    break;
                case 2:
                    this.playerList.forEach(element => {
                        if (!element.Roles) {
                            element.adminData.rank > 0;
                        }
                        var searchRole = element.Roles.findIndex(item => item == "admin");
                        if (searchRole != -1) {
                            element.show = true
                        }
                    });
                    break;
                case 3:
                    this.playerList.forEach(element => {
                        if (!element.Roles) {
                            element.Roles = [];
                        }
                        var searchRole = element.Roles.findIndex(item => item == "police");
                        if (searchRole != -1) {
                            element.show = true
                        }
                    });
                    break;
                default:
                    break;
            }
        },
        showAllArray(array) {
            array.forEach(element => {
                element.show = true;
            });
        },
        hideAllArray(array) {
            array.forEach(element => {
                element.show = false;
            });
        },
        getDate(date) {
            var newdate = new Date(date);
            if (newdate) {
                var year = newdate.getFullYear();
                var month = ("0" + (newdate.getMonth() + 1)).slice(-2);
                var day = ("0" + newdate.getDate()).slice(-2);
                return day + "." + month + "." + year;
            } else {
                newdate = "01.01.1900";
                return newdate;
            }
        },
        getSex(sex) {
            if (sex) {
                return ""
            } else {
                return ""
            }
        },
        byField(field) {
            return (a, b) => a[field] > b[field] ? 1 : -1;
        },
        changePlayerSort(numb) {
            this.activeSort = numb;
            if (numb == 1) {
                this.playerList.sort(this.byField('Name'));
            }
            if (numb == 2) {
                this.playerList.sort(this.byField('Id'));
            }
        },
        changeCompact() {
            this.compact = !this.compact;
        },
        useCommand(index) {
            axios.post('https://eclipse/AmdCommand', {index}).then(() => { return; }).catch(function () { console.log() });
        },
        usePlayerCommand(id, command) {
            axios.post('https://eclipse/AmdCommand', {
                    UserId: id,
                    index: command
            }).then(() => { return; }).catch(function () { console.log() });
        },
        useAdminCommand(index) {
            switch (index) {
                case 0:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "CreateVehicle",
                        VehicleData: this.createVehicleModel
                    }).then(() => { return; }).catch(function () { console.log() });
                    //  
                    this.createVehicleModel.Hash;
                    this.createVehicleModel.Color;
                    this.createVehicleModel.Plate;
                    break;
                case 1:
                    //    
                    axios.post('https://eclipse/AmdCommand', {
                        index: "DeleteVehicle",
                        CarIndex: this.deleteCarIndex
                    }).then(() => { return; }).catch(function () { console.log() });
                    this.deleteCarIndex
                    break;
                case 2:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "DeleteNearestVehicle",
                    }).then(() => { return; }).catch(function () { console.log() });
                    //   
                    break;
                case 3:
                     axios.post('https://eclipse/AmdCommand', {
                        index: "CreateProp",
                        PropHash: this.createPropModel.Hash
                    }).then(() => { return; }).catch(function () { console.log() });
                
                    break;
                case 4:
                    //    
                    axios.post('https://eclipse/AmdCommand', {
                        index: "DeleteProp",
                        PropHash: this.deletePropIndex
                    }).then(() => { return; }).catch(function () { console.log() });
                    break;
                case 5:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "DeletePropById",
                        PropHash: this.deletePropIndexById
                    }).then(() => { return; }).catch(function () { console.log() });
                    break;
                case 6:
                    axios.post('https://eclipse/AmdCommand', {
                        index: "HealVehicle",
                    }).then(() => { return; }).catch(function () { console.log() });
                    break;  
                default:
                    break;
            }
        },
        changeGameWeather(index) {
            this.showWeatherDropdown = false;
            this.weather = this.weatherList[index];
        },
        sendWeatherToBackend() {          
            if (this.weather) {
                axios.post('https://eclipse/ChangeGameWeather', {
                    Weather: this.weather.value,
                }).then(() => { return; }).catch(function () { console.log() });
            }
            
        },
        syncServerTime() {
            axios.post('https://eclipse/SyncServerTime', {
                    Weather: this.weather.value,
                }).then(() => { return; }).catch(function () { console.log() });
        },
        changeServerTime() {
            this.timeHours = parseInt(this.timeHours);
            this.timeMinutes = parseInt(this.timeMinutes);
            var testHours = Number.isInteger(this.timeHours);
            var testMinutes = Number.isInteger(this.timeMinutes)

            if (!testHours || !testMinutes) {
                this.timeHours = "";
                this.timeMinutes = "";
                return
            }
            if (this.timeHours > 24 || this.timeHours < 0) {
                return
            }
            if (this.timeMinutes > 60 || this.timeMinutes < 0) {
                return
            }
            
            axios.post('https://eclipse/ChangeGameTime', {
                Hours: this.timeHours,
                Minutes: this.timeMinutes
            }).then(() => { return; }).catch(function () { console.log() });
        },
        isAN(value) {
            return (value instanceof Number||typeof value === 'number') && !isNaN(value);
        },
        openModalWindowA() {
            this.showGiveItemWindowA = true;
        },
        openModalWindowE() {
            this.showGiveItemWindowE = true;
        },
        giveItemToPlayer() {
            var data = {
                ItemId: this.giveItemModel.Name.trim().toLowerCase(),
                Count: this.giveItemModel.Count,
            }
            axios.post('https://eclipse/AmdCommand', {
                index: "addItem",
                UserId: this.activePlayer.Id,
                Item: data
            }).then(() => { return; }).catch(function () { console.log() });
            this.giveItemModel.Name = "";
            this.giveItemModel.Count = 0;
        },
        giveItemToAdmin() {
            var data = {
                ItemId: this.giveItemModel.Name.trim().toLowerCase(),
                Count: this.giveItemModel.Count,
            }
            axios.post('https://eclipse/AmdCommand', {
                index: "addItem",
                UserId: this.playerData.id,
                Item: data
            }).then(() => { return; }).catch(function () { console.log() });
            this.giveItemModel.Name = "";
            this.giveItemModel.Count = 0;
        },
        giveHashToPlayer() {
            if (this.giveHash) {
                axios.post('https://eclipse/AmdCommand', {
                    index: "addHash",
                    UserId: this.activePlayer.Id,
                    Hash: this.giveHash
                }).then(() => { return; }).catch(function () { console.log() });
            }
        },
        kickPlayerFromServer() {
            axios.post('https://eclipse/AmdCommand', {
                index: "kickPlayer",
                UserId: this.activePlayer.Id,
                Reason: this.kickReason
            }).then(() => { return; }).catch(function () { console.log() });
            this.kickReason = "";
            this.showGiveItemWindow = false;
            
        },
        banPlayerOnServer() {
            axios.post('https://eclipse/AmdCommand', {
                index: "ban",
                UserId: this.activePlayer.Id,
                Reason: this.banReason
            }).then(() => { return; }).catch(function () { console.log() });
            this.banReason = "";
            this.banTime = 0;
            this.showGiveItemWindowD = false;
            
        },
        editPlayerInfo() {
            this.showEditPlayerInfoWindow = true;
            this.editPlayerInfoModel = JSON.parse(JSON.stringify(this.activePlayer))
        },
        editPlayerAuto(index) {
            this.showEditPlayerAutoWindow = true;
            this.activeAutoIndex = index;
            this.editPlayerAutoModel = JSON.parse(JSON.stringify(this.activePlayer.cars[index]))
        },
        savePlayerInfoEdit() {
            this.activePlayer.IdCard.Name = this.editPlayerInfoModel.IdCard.Name;
            //this.activePlayer.IdCard.Age = this.editPlayerInfoModel.IdCard.Age;
            this.activePlayer.IdCard.Phone = this.editPlayerInfoModel.IdCard.Phone;
            this.activePlayer.IdCard.Lastname = this.editPlayerInfoModel.IdCard.Lastname;
            this.activePlayer.IdCard.Adress = this.editPlayerInfoModel.IdCard.Adress;
            this.activePlayer.IdCard.About = this.editPlayerInfoModel.IdCard.About;
            axios.post('https://eclipse/AmdCommand', {
                index: "SavePlayerInfoEdit",
                UserId: this.activePlayer.Id,
                IdCard: this.activePlayer.IdCard
            }).then(() => { return; }).catch(function () { console.log() });
            this.showEditPlayerInfoWindow = false;
            
        },
        savePlayerAuto() {
            this.activePlayer.cars[this.activeAutoIndex] = this.editPlayerAutoModel;
            this.showEditPlayerAutoWindow = false;
        },
        editPlayerRoles() {
            this.showEditPlayerRolesWindow = true;
            if (!this.activePlayer.Roles) {
                this.activePlayer.Roles = [];
            }
        },
        editPlayerDimension() {
            this.showEditPlayerDimensionWindow = true;
            this.newPlayerDimension = this.activePlayer.Dimension;
        },
        resetPlayerDimension() {
            // 
        },
        changePlayerDimension() {
            axios.post('https://eclipse/changePlayerDimension', {
                    Dimension: this.newPlayerDimension,
                    UserId: this.activePlayer.Id
                }).then(() => { return; }).catch(function () { console.log() });
            this.showEditPlayerDimensionWindow = false;
        },
        editPlayerWeapon(index) {
            this.showEditWeaponWindow = true;
            this.activeWeaponIndex = index;
            this.editPlayerWeaponModel = JSON.parse(JSON.stringify(this.activePlayer.Weapons[index]))
        },
        deletePlayerWeapon() {
            this.activePlayer.Weapons.splice(this.activeWeaponIndex, 1);
            this.showEditWeaponWindow = false;
        },
        savePlayerWeapon() {
            this.activePlayer.Weapons[this.activeWeaponIndex] = this.editPlayerWeaponModel;
            this.showEditWeaponWindow = false;
        },
        addNewWeaponToPlayer() {
            this.showAddNewWeaponWindow = true;
            this.newPlayerWeaponModel = {
                Hash: "",
                Nubmer: "",
                SellerId: "",
            }
        },
        saveNewWeaponToPlayer() {
            var isWeaponExist = this.findWeaponByHash(this.newPlayerWeaponModel.Hash);
            if (isWeaponExist) {
                if (!this.activePlayer.Weapons) {
                    this.activePlayer.Weapons = [];
                }
                this.showWeaponError = false;
                this.activePlayer.Weapons.push(this.newPlayerWeaponModel);
                this.showAddNewWeaponWindow = false;
            } else {
                this.showWeaponError = true;
            }
        },
        findWeaponByHash(hash) {
            var weapon = this.weaponNamesList.find(item=> item.id == hash)
            if (!weapon) {
                weapon = false;
            }
            return weapon
        },
        addNewAutoToPlayer() {
            this.showAddNewAutoWindow = true;
            this.newPlayerAutoModel = {
                name: "",
                carId: "",
                type: "",
                color: "",
                number: "",
                sellerId: "",
            };
        },
        saveNewAutoToPlayer() {
            if (!this.activePlayer.cars) {
                this.activePlayer.cars = [];
            }
            this.activePlayer.cars.push(this.newPlayerAutoModel);
            this.showAddNewAutoWindow = false;
        },
        deletePlayerAuto() {
            this.activePlayer.cars.splice(this.activeAutoIndex, 1);
            this.showEditPlayerAutoWindow = false;
        },
        addNewRoleToPlayer() {
            var role = this.newPlayerRole.toLowerCase();
            var isExist = this.activePlayer.Roles.findIndex(elem => elem == role);
            if (role != "" && isExist == -1) {
                this.activePlayer.Roles.push(role);
                axios.post('https://eclipse/AmdCommand', {
                    index: "AddNewRoleToPlayer",
                    UserId: this.activePlayer.Id,
                    Role: role
                }).then(() => { return; }).catch(function () { console.log() });
            }
            this.newPlayerRole = "";
            
        },
        deletePlayerRole(index) {
            axios.post('https://eclipse/AmdCommand', {
                    index: "DeletePlayerRole",
                    UserId: this.activePlayer.Id,
                    Role: this.activePlayer.Roles[index]
            }).then(() => { return; }).catch(function () { console.log() });
            this.activePlayer.Roles.splice(index, 1);
        },
        searchPlayer() {
            var searchText = this.searchFieldModel;
            if (searchText) {
                var exp = new RegExp(searchText, "igm");
                this.playerList.forEach((element) => {
                    var string = element.Id.toString()
                        + element.IdCard.Name
                        + element.IdCard.Lastname
                        + element.IdCard.Phone
                        + element.IdCard.About
                        + element.IdCard.Address
                        + this.getSex(element.IdCard.Sex)
                    var result = string.search(exp);
                    if (result == -1) {
                        element.show = false;
                    } else {
                        element.show = true;
                    }
                });
            } else {
                this.showAllArray(this.playerList);
            }
        },
        refreshAdminData() {
            this.iconLoading = true;
            //    
            setTimeout(() => {
                this.iconLoading = false;
            }, 3000);
        },
        deleteChatMessage(index) {
            this.gameChatData.splice(index, 1);
        },
        deletePoliceCall(index) {
            this.policeCallData.splice(index, 1);
        },
        deleteMedicCall(index) {
            this.medicalCallData.splice(index, 1);
        },
        getStatus(index){
            if (!index) {
                index = 0;
            }
            return this.statusList[index];
        },
        showFullAdminCall(index) {
            this.changePage(51);
            this.activeAdminCall = this.adminCalls[index];
            if (!this.activeAdminCall.OnScene) {
                this.activeAdminCall.OnScene = [];
            }
        },
        changeStatus(index) {
            this.activeAdminCall.Status = index;
            this.showAdminDropdown = false;
        },
        adminCallCommand(command) {
            switch (command) {
                case 0:
                    var myId = this.playerData.id;
                    var index = this.activeAdminCall.OnScene.findIndex(item => item == this.playerData.id);
