    methods: {
        startJob() {
            axios.post('https://eclipse/ElectricStartWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
        },
        stopJob() {
            axios.post('https://eclipse/ElectricStopWorkDay', {
            }).then(() => { return; }).catch(function () { console.log() });
