<template>
    <div class="emsjob">
        <transition name="transition_03">
            <div class="main_content bg_white" v-if="page == 0">
                <h2 class="text-center">Emergency Medical Service</h2>

                <p>Per Apollinem medicum et Aesculapium, Hygiamque et Panaceam juro, deos deasque omnes testes citans, mepte viribus et judicio meo hos jusjurandum et hanc stipulationem plene praestaturum.</p>
                <p>Illum nempe parentum meorum loco habiturum spondeo, qui me artem istam docuit, eique alimenta impertirurum, et quibuscunque opus habuerit, suppeditaturum.</p>
                <p>Victus etiam rationem pro virili et ingenio meo aegris salutarem praescripturum a pemiciosa vero et improba eosdem prohibiturum. Nullius praeterea precibus adductus, mortiferum medicamentum cuique propinabo, neque huius rei consilium dabo. Caste et sancte colam et artem meam.</p>
                <p>Quaecumque vero in vita hominum sive medicinam factitans, sive non, vel videro, vel audivero, quae in vulgus efferre non decet, ea reticebo non secus atque arcana fidei meae commissa.</p>
                <p>Quod si igitur hocce jusjurandum fideliter servem, neque violem, contingat et prospero successu tarn in vita, quam in arte mea fruar et gloriam immortalem gentium consequar. Sine autem id transgrediar et pejerem contraria hisce mihi eveniam.</p>
                <div class="menu">
                    <button class="btn btn-primary" @click="startDuty()" v-if="!playerOnDuty"> </button>
                    <button class="btn btn-primary" @click="exitDuty()" v-if="playerOnDuty">  </button>
                    <button class="btn btn-primary" @click="openCad()"> </button>
                    <button class="btn btn-primary" @click="changePage(1)"></button>
                    <button class="btn btn-secondary" @click="exit()"></button>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="main_content bg_white" v-if="page == 1">
                <h2 class="text-center"></h2>
                <div class="player_info" v-if="emsDataExist">
                    <h4><b> :</b> {{playerData.name}}</h4>
                    <h4><b> :</b> {{playerData.phone}}</h4>
                    <h4><b> :</b> 0 </h4>
                </div>
                <div class="menu">
                    <button class="btn btn-secondary" @click="changePage(0)"></button>
                </div>
            </div>
        </transition>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'PoliceJob',
    props: {
        playerData: Object
    },
    components: {
        
    },
    data() {
        return {
            playerOnDuty: false,
            emsDataExist: false,
            page: 0
        }
    },
    methods: {
        changePage(index) {
            if (this.page != index) {
                this.page = index;
            }
        },
        startDuty() {
            this.playerOnDuty = true;
            axios.post('https://eclipse/EmsOnDuty', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            console.log('  ');
        },
        exitDuty() {
            axios.post('https://eclipse/EmsOffDuty', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            this.playerOnDuty = false;
            console.log('  ');
        },
        exit() {
            axios.post('https://eclipse/ExitEmsRec', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            console.log('');
        },
        openCad() {
            this.$parent.showJob = "";
            this.$parent.$parent.showPage = "MedicalCad";
            //     , 
        }
    },
    mounted() {
         window.addEventListener("message",(event) => {
             if (event.data.type == "EmsData") {
                // this.playerData.name = event.data.name
                // this.playerData.phone = event.data.phone
                // this.playerOnDuty = event.data.duty
             }
         }, false);
        if (this.playerData.medicData) {
            this.emsDataExist = true;
        }
        if (this.playerData.medicData && this.playerData.medicData.onDuty != 0) {
            this.playerOnDuty = true;
        } else {
            this.playerOnDuty = false;
        }

    }
}

</script>