        },
        addPlayerLicense() {
            var license = this.newPlayerLicense.toLowerCase().trim();
            var isExist = this.activePlayerLicense.Licenses.findIndex(elem => elem == license);
            if (license != "" && isExist == -1) {
                this.activePlayerLicense.Licenses.push(license);
            }
            this.newPlayerLicense = "";
        },
