<template>
    <div class="policejob">
        <transition name="transition_03">
            <div class="main_content bg_white" v-if="page == 0">
                <h2 class="text-center">  LSPD</h2>
                <p>    <b></b>   .           .          .           .</p>
                <p>    <b> </b>: -    ,               ,      .</p>
                <p>        <b></b>,    .       ,   .</p>
                <div class="menu">
                    <button class="btn btn-primary" @click="startDuty()" v-if="!playerOnDuty"> </button>
                    <button class="btn btn-primary" @click="exitDuty()" v-if="playerOnDuty">  </button>
                    <button class="btn btn-primary" @click="openCad()"> </button>
                    <button class="btn btn-primary" @click="changePage(1)"></button>
                    <button class="btn btn-secondary" @click="exit()"></button>
                </div>
            </div>
        </transition>

        <transition name="transition_03">
            <div class="main_content bg_white" v-if="page == 1">
                <h2 class="text-center"></h2>
                <div class="player_info" v-if="policeDataExist">
                    <h4><b> :</b> {{playerData.name}}</h4>
                    <h4><b> :</b> {{playerData.phone}}</h4>
                    <h4><b>:</b> {{playerData.policeData.rank}}</h4>
                    <h4><b>:</b> {{playerData.policeData.callsign}}</h4>
                    <h4><b> :</b> 0 </h4>
                </div>
                <div class="menu">
                    <button class="btn btn-secondary" @click="changePage(0)"></button>
                </div>
            </div>
        </transition>
        <div class="police_girl"></div>
    </div>
</template>

<script>
import axios from "axios";
export default {
    name: 'PoliceJob',
    props: {
        playerData: Object
    },
    components: {
        
    },
    data() {
        return {
            playerOnDuty: false,
            policeDataExist: false,
            page: 0
        }
    },
    methods: {
        changePage(index) {
            if (this.page != index) {
                this.page = index;
            }
        },
        startDuty() {
            this.playerOnDuty = true;
            axios.post('https://eclipse/PoliceOnDuty', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            console.log('  ');
        },
        exitDuty() {
            axios.post('https://eclipse/PoliceOffDuty', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            this.playerOnDuty = false;
            console.log('  ');
        },
        exit() {
            axios.post('https://eclipse/ExitPolicRec', {
            }).then(() => { return; }).catch(function () { console.log() });
            this.$parent.showJob = "";
            console.log('');
        },
        openCad() {
            this.$parent.showJob = "";
            //this.$parent.$parent.showPage = "PoliceCad";
            axios.post('https://eclipse/OpenCad', {
            }).then(() => { return; }).catch(function () { console.log() });
            
            //     , 
        }
    },
    mounted() {
         window.addEventListener("message",(event) => {
            if (event.data.type == "PoliceData") {
            //    this.playerData.name = event.data.name
            //    this.playerData.phone = event.data.phone
            //    this.playerOnDuty = event.data.duty
            }
        }, false);
        if (this.playerData.policeData) {
            this.policeDataExist = true;
        }
        if (this.playerData.policeData && this.playerData.policeData.onDuty != 0) {
            this.playerOnDuty = true;
        } else {
            this.playerOnDuty = false;
        }

    }
}

</script>